@import "https://fonts.googleapis.com/css2?family=Manrope:wght@200;400;700&display=swap";
:root {
  --pure-white: #fff;
  --radial-bg: radial-gradient(50% 48.71% at 50% 50%, #fff175 0%, #ffe500 100%);
  --grey-text: #6f6f6f;
  --white-text: #fffef3;
  --white-text-hover: #fffbd3;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--grey-text);
  margin: 0;
  font-family: Manrope, sans-serif;
  font-size: 20px;
}

h1, h2, h3, h4, h5, h6, p, ul, li, a {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  display: block;
}

.hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.hero {
  display: flex;
  overflow: hidden;
}

.hero__array {
  width: 40vw;
  height: 100vh;
  background: var(--white-text);
  z-index: 1002;
  flex-direction: column;
  justify-content: center;
  padding: 80px;
  display: flex;
  position: relative;
}

.hero__array-open {
  width: 100vw;
}

.animateForvard {
  animation: animationOnMoveForvard .7s ease-in;
}

.animateBack {
  animation: animationOnMoveBack .7s ease-in;
}

@keyframes animationOnMoveForvard {
  0% {
    width: 40vw;
  }

  100% {
    width: 100vw;
  }
}

@keyframes animationOnMoveBack {
  0% {
    width: 100vw;
  }

  100% {
    width: 40vw;
  }
}

.animateForvardMain {
  animation: animationOnMoveForvardMain .7s ease-in;
}

.animateBacMain {
  animation: animationOnMoveBackMain .7s ease-in;
}

@keyframes animationOnMoveForvardMain {
  0% {
    width: 60vw;
  }

  100% {
    width: 0;
  }
}

@keyframes animationOnMoveBackMain {
  0% {
    width: 0;
  }

  100% {
    width: 60vw;
  }
}

.hero__paragraph {
  margin-bottom: 60px;
  line-height: 34px;
}

.hero__button {
  box-sizing: border-box;
  letter-spacing: .1em;
  background-color: var(--white-text);
  width: 200px;
  height: 46px;
  cursor: pointer;
  border: none;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  font-size: 22px;
  font-weight: 200;
  line-height: 27px;
  transition: all .3s;
  display: flex;
  box-shadow: -1px 25px 24px -21px rgba(0, 0, 0, .31);
}

.hero__button:hover {
  box-shadow: -1px 25px 24px -21px rgba(0, 0, 0, .7);
}

.hero__main {
  background: var(--radial-bg);
  width: 60vw;
  height: 100vh;
  position: relative;
}

.hero__main-open {
  width: 0;
}

.hero__content {
  z-index: 1001;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero__content-open {
  display: none;
}

.hero__title {
  margin-bottom: 24px;
  font-size: 22px;
  font-weight: 400;
  line-height: 27px;
}

.hero__list {
  gap: 20px;
  display: flex;
}

.hero__list :hover {
  background-color: var(--white-text-hover);
  box-shadow: -1px 25px 24px -21px rgba(0, 0, 0, .7);
}

.hero__item {
  letter-spacing: .1em;
  background-color: var(--white-text);
  width: 154px;
  height: 46px;
  cursor: pointer;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  font-size: 22px;
  font-weight: 200;
  line-height: 27px;
  transition: all .3s;
  display: flex;
  box-shadow: -1px 25px 24px -21px rgba(0, 0, 0, .31);
}

.hero__image {
  margin-right: 10px;
}

.hero__image--salat {
  width: 30px;
  height: 30px;
  display: block;
}

.hero__image--omlet, .hero__image --drink {
  width: 26px;
  height: 26px;
  display: block;
}

.hero__svg {
  letter-spacing: .2em;
  z-index: 1000;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  position: absolute;
}

.hero__svg--right-small {
  width: 80%;
  height: 80%;
  opacity: .6;
  animation: rotateLeft 20s linear infinite;
  top: 100px;
  left: 100px;
}

.hero__svg--right-big {
  width: 60%;
  height: 60%;
  opacity: .55;
  animation: rotateRight 20s linear infinite;
  top: 40px;
  left: 40px;
}

.hero__svg--right-smaller {
  width: 30%;
  height: 30%;
  opacity: .5;
  animation: rotateLeft 20s linear infinite;
  top: 20px;
  left: 20px;
}

@keyframes rotateLeft {
  to {
    transform: rotate(360deg);
  }
}

@keyframes rotateRight {
  to {
    transform: rotate(-360deg);
  }
}

.hero__text {
  fill: #fff;
  font-size: 26px;
}

